import React from "react";
import { getFeedbackById } from "@/lib/getFeedbackById";
import { generateParamsFromFeedbacks } from "@/lib/generateParams";
import { notFound } from "next/navigation";
export async function generateStaticParams() {
  return await generateParamsFromFeedbacks();
}

const page = async ({ params }: { params: { id: string } }) => {
  const feedback = await getFeedbackById(params.id);

  if (!feedback) return notFound();
  return (
    <div className="p-10">
      <h1>{feedback.title}</h1>
      <p>
        <strong>Category:</strong> {feedback.category}
      </p>
      <p>
        <strong>Status:</strong> {feedback.status}
      </p>
      <p>
        <strong>Description:</strong> {feedback.description}
      </p>
    </div>
  );
};

export default page;
